// @flow

import styled, { css } from 'styled-components';

export const defaultPipSize = '8px';

const PipComponent = styled.div`
  border: ${({ theme }) => theme.pipSize} inset;
  border-bottom-style: solid;
  border-color: transparent transparent ${({ theme }) => theme.palette.primary};
  border-top-width: 0;
  position: absolute;
  top: calc(1rem + 1px - ${({ theme }) => theme.pipSize});
  transition: left 500ms ease-in-out;

  ${({ coolChildrenLength, theme, activeIndex }) => css`
    left: calc(
      ((100% / ${coolChildrenLength}) * ${activeIndex}) +
        ((100% / ${coolChildrenLength}) / 2) - ${theme.pipSize}
    );
  `};

  ${({ theme }) =>
    theme &&
    css`
      @media (max-width: ${theme.breakpointForTinyMode}) {
        left: calc(50% - ${theme.pipSize});
      }
    `};
`;

export default PipComponent;
