// @flow

import * as React from 'react';
import styled, { css } from 'styled-components';

export const StyledCoolChild = styled.button.attrs({ type: 'button' })`
  background-color: ${({ theme }) => theme.palette.lightGray};
  border: 1px solid ${({ theme }) => theme.palette.border};
  color: ${({ theme }) => theme.palette.darkGray};
  flex: 1;
  margin: -1px;
  overflow: hidden;
  padding: 1rem;
  text-align: center;

  &:first-child {
    border-radius: 4px 0 0 4px;
  }

  &:last-child {
    border-radius: 0 4px 4px 0;
  }

  ${({ active }) =>
    active &&
    css`
      background-color: ${({ theme }) => theme.palette.primary};
      border: 1px solid ${({ theme }) => theme.palette.primary};
      color: ${({ theme }) => theme.palette.white};
    `};
`;

export type $Props = {
  children?: React.Node,
  label?: string,
};

function CoolChild(props: $Props) {
  const { children, label, ...rest } = props;

  return <StyledCoolChild {...rest}>{label}</StyledCoolChild>;
}

export default CoolChild;
